import './App.css';
import IENavbar from './IENavbar';
import IEfooter from './IEfooter';
import {BrowserRouter as Router, HashRouter, Route, Switch} from 'react-router-dom';

import Home from './Home';
import Games from './Games';
import Comics from './Comics';
import About from './About';
import Contact from './Contact';

const rPath = process.env.REACT_APP_FOR_PATH;

function App() {
  return (
    
      <div className="App" style={{ backgroundImage: "url(/images/bg.jpg)"}}>
        <Router>
          <IENavbar/>
          <div className="content">
            <Switch>
              <Route exact path= {rPath + "/"}>
                <IENavbar props = {"/"} />
                <Home />
              </Route>
              <Route path= {rPath + "/games"}>
                <IENavbar props = {"/games"}/>
                <Games />
              </Route>
              <Route path= {rPath + "/comics"}>
                <IENavbar props = {"/comics"}/>
                <Comics />
              </Route>
              <Route path= {rPath + "/about"}>
                <IENavbar props = {"/about"}/>
                <About />
              </Route>
              <Route path= {rPath + "/contact"}>
                <IENavbar props = {"/contact"}/>
                <Contact />
              </Route>  
            </Switch>
            <IEfooter/>
          </div>
        </Router>
      </div>
  );
}

export default App;
