import { Navbar, Nav, Container } from 'react-bootstrap';

import { NavLink } from "react-router-dom";


function IENavbar(props){
    var logoStroke = "/logo_stroke.png";
    var logo = "/logo.png"
    var srcV = logoStroke;
    var isNotHome = false;
    var p = document.getElementById(props);
    var e;
    
    if(props.props !== "/"){
        srcV = logo;
        isNotHome = true;
    }

    return (
        <>
            <Navbar 
                className = "IENav"
                expand="lg"
                variant="dark"
                fixed= "top">
                <Container>
                    <Nav className="LinkNav">
                        <NavLink to="/"> 
                            <img
                                alt="IEye Logo"
                                src= {srcV}
                                width="30"
                                height="30"
                                className="d-inline-block"
                                

                                onMouseOver= {e => (isNotHome) ? (e.currentTarget.src = logoStroke) : ""}
                                onMouseOut= {e => (isNotHome) ? (e.currentTarget.src = logo): ""}
                            />
                        </NavLink>
                        <NavLink to="/games" activeClassName = "ActiveLink" className = "UnactiveLink">Games</NavLink>
                        <NavLink to="/comics" activeClassName = "ActiveLink" className = "UnactiveLink">Comics</NavLink>
                        <NavLink to="/about" activeClassName = "ActiveLink" className = "UnactiveLink">About</NavLink>
                        <NavLink to="/contact" activeClassName = "ActiveLink" className = "UnactiveLink">Contact</NavLink>
                    </Nav>
                </Container>
            </Navbar>
            
        </>
    );
}


export default IENavbar;